@import "../../scss/_variable";

.remember {
     position: fixed;
     font-size: 10px;
     padding: 2px 18px;
     color: $TextClr;
     bottom: 0;
     left: 0;
     right: 0;
     z-index: 999;
     line-height: 1.2;
     background-color: white;
     box-shadow: 0 0 3px $newMain3;
     & a {
          color: $TextClr;
     }
}
